.o-button {
  position: relative;
  cursor: pointer;
  font-size: 1.6rem;
  padding: 1rem 2rem;
  border-radius: 5rem;
  border: 2px solid rgba(5, 7, 10, 0.15);
  color: rgba(5, 7, 10, 0.5);
  background: transparent;
  transition: background-color 0.3s ease;
}
.o-button:disabled {
  cursor: not-allowed;
  border: 2px solid rgba(5, 7, 10, 0.05);
  color: rgba(5, 7, 10, 0.25);
}
.o-button:hover {
  background-color: rgba(5, 7, 10, 0.05);
}
.o-button:focus {
  box-shadow: 0 0 5px #3773d7;
}
.o-button--link {
  padding: 1rem 0;
  background-color: transparent;
  border-color: transparent;
  border-radius: none;
}
.o-button--link:focus {
  box-shadow: none;
}
.o-button--link:hover {
  background-color: transparent;
  border-color: transparent;
}
.o-button--link:hover:after {
  bottom: .8rem;
  position: absolute;
  content: "";
  display: block;
  width: 100%;
  height: .2rem;
  background-color: currentColor;
}
.o-button--link--red {
  color: #f53c1e;
}
.o-button--blue {
  border-color: #3773d7;
  color: #3773d7;
  transition: color 0.5s ease, background-color 0.5s ease;
}
.o-button--blue:focus {
  background-color: rgba(55, 115, 215, 0.15);
  box-shadow: 0 0 5px #3773d7;
}
.o-button--blue:hover,
.o-button--blue:active {
  background-color: #3773d7;
  color: #ffffff;
}
.o-button--red {
  border-color: #f53c1e;
  color: #f53c1e;
  transition: color 0.5s ease, background-color 0.5s ease;
}
.o-button--red:focus {
  background-color: rgba(245, 60, 30, 0.15);
  box-shadow: 0 0 5px #f53c1e;
}
.o-button--red:hover,
.o-button--red:active {
  background-color: #f53c1e;
  color: #ffffff;
}
.o-button--space-top {
  margin-top: 2rem;
}

